<script src="../../api/getData.js"></script>
<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-form :inline="true" :model="formInline" class="demo-form-inline">
                <el-form-item label="账号">
                    <el-input v-model="formInline.account" placeholder="账号"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onQuery">查询</el-button>
                </el-form-item>
            </el-form>
            <el-table
                    :data="tableData"
                    border
                    style="width: 100%">
                <el-table-column
                        prop="id"
                        label="ID"
                        min-width="100">
                </el-table-column>
                <el-table-column
                        prop="category"
                        label="类别"
                        min-width="180">
                </el-table-column>
                <el-table-column
                        prop="rank"
                        label="内容"
                        min-width="180">
                </el-table-column>
                <el-table-column
                        prop="createTime"
                        label="创建时间"
                        min-width="200">
                </el-table-column>
                <el-table-column
                        prop="updateTime"
                        label="更新时间"
                        min-width="200">
                </el-table-column>
                <el-table-column
                        prop="operation"
                        label="操作"
                        min-width="104">
                    <template slot-scope="scope">
                        <el-button type="text" @click="undoAuth(scope.row)" size="small">撤销权限</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="Pagination">
                <el-pagination
                        @current-change="handleCurrentChange"
                        :current-page.sync="currentPage1"
                        :page-size="10"
                        layout="total, prev, pager, next"
                        :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    import {getAuthList,undoAuth} from '@/api/getData'

    export default {
        data() {
            return {

                formInline: {
                    account: ''
                },
                tableData: [],
                category: '',
                total: 0,
                currentPage1: 1,
                dialogFormVisible: false,
                form: {

                    id: '',
                    category: '',
                    rank: '',
                    score: '',

                },
                formLabelWidth: '50px'
            }
        },
        beforeCreate: async function () {

        },
        methods: {
            async onQuery() {
                this.currentPage1 = 1;
                await this.getData(1);
            }, async handleCurrentChange() {
                await this.getData(this.currentPage1);
            }, async getData(page) {

                const data = {
                    account:this.formInline.account,
                    page:page
                }
                const user = await getAuthList(data)
                if (user.code == '0200') {

                    if(user.data.length==0){
                        this.$message({
                            type: 'error',
                            message: '暂无数据'
                        });
                    }
                    this.tableData = user.data;
                    this.total = user.data[0].rows;
                } else {
                    this.$message({
                        type: 'error',
                        message: user.msg
                    });
                }
            }, handleClick(row) {

                this.dialogFormVisible = true;
                this.form.id = row.id;
                this.form.category = row.category;
                this.form.rank = row.rank;
                this.form.score = row.score;

            }, async undoAuth(row) {

                this.$confirm('确认撤销权限？')
                    .then(async _ => {
                        const res = await undoAuth({'id':row.id});
                        if (res.code == '0200') {
                            this.$message({
                                type: 'success',
                                message: '已成功撤销权限'
                            });
                            await this.getData(this.currentPage1);
                        } else {
                            this.$message({
                                type: 'error',
                                message: res.msg
                            });
                        }
                    })
                    .catch(_ => {

                    });
            }
        }
    }
    ;
</script>
<style lang="less" scoped>
    @import '../../style/mixin';

    /*.el-form-item {
        margin-right: 30px;
        margin-bottom: 0;
        height: 50px;
        width: 49%;
    }*/

    .query {
        position: absolute;
        right: 300px;
    }

    .table_container {
        padding: 20px;
    }

    .Pagination {
        display: flex;
        justify-content: flex-start;
        margin-top: 8px;
    }
</style>




