<script src="../../api/getData.js"></script>
<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-form ref="form" :model="sizeForm" label-width="80px" size="mini">
                <el-form-item label="账号">
                    <el-input v-model="sizeForm.account"></el-input>
                </el-form-item>
                <el-form-item label="姓名">
                    <el-input v-model="sizeForm.name"></el-input>
                </el-form-item>
                <el-form-item label="学院">
                    <el-input v-model="sizeForm.college"></el-input>
                </el-form-item>
                <el-form-item label="专业">
                    <el-input v-model="sizeForm.profession"></el-input>
                </el-form-item>
                <el-form-item label="班级">
                    <el-input v-model="sizeForm.classroom"></el-input>
                </el-form-item>

                <el-form-item label="身份">
                    <el-select v-model="sizeForm.level" placeholder="请选择用户身份">
                        <el-option label="学生" value="1"></el-option>
                        <el-option label="教师" value="2"></el-option>
                        <el-option label="专业领导" value="3"></el-option>
                        <el-option label="学院领导" value="4"></el-option>
                        <el-option label="审核人员" value="5"></el-option>
                        <el-option label="管理员" value="9"></el-option>
                    </el-select>
                </el-form-item>
                <el-upload
                    class="upload-demo"
                    :action="uploadFileAddress"
                    multiple
                    :show-file-list="false"
                    :headers="uploadFileHeaders"
                    :limit="1"
                    :before-upload="beforeUpload"
                    :on-success="successFileUpload"
                    :file-list="fileList">
                    <el-button class="uploadFileButton" type="success" :style="{'display':'none'}"></el-button>
                </el-upload>
                <el-form-item size="large">
                    <el-button type="primary" @click="onSubmit">立即提交</el-button>
                    <el-button type="info" @click="onReset">重置</el-button>
                    <el-button type="success" @click="onImport">批量添加</el-button>
                </el-form-item>
            </el-form>

        </div>
    </div>
</template>

<script>
    import {serverUrl} from '@/config/env'
    import {addUser} from '@/api/getData'

    export default {
        data() {
            return {
                sizeForm: {
                    account: '',
                    name: '',
                    college: '',
                    profession: '',
                    classroom: '',
                    level: '',
                },
                fileList: [],
                uploadFileAddress: serverUrl+'/user/batchAddUser',
                uploadFileHeaders: {
                    'token': sessionStorage.getItem("token")
                }

            };
        },
        methods: {
            async onSubmit() {
                const data = {

                    account: this.sizeForm.account,
                    name: this.sizeForm.name,
                    college: this.sizeForm.college,
                    profession: this.sizeForm.profession,
                    classroom: this.sizeForm.classroom,
                    level: this.sizeForm.level,
                }
                const res = await addUser(data)
                if (res.code == '0200') {
                    this.$message({
                        type: 'success',
                        message: '添加成功'
                    });
                } else {
                    this.$message({
                        type: 'error',
                        message: '添加失败 :' + res.msg
                    });
                }

            }, onReset() {

                this.sizeForm.account = '';
                this.sizeForm.name = '';
                this.sizeForm.college = '';
                this.sizeForm.profession = '';
                this.sizeForm.classroom = '';
                this.sizeForm.level = '';

            },
            onImport() {
                document.getElementsByClassName("uploadFileButton")[0].click();
            },
            beforeUpload(file) {

                var fileName = file.name;
                var suffix = fileName.split(".")[1];
                if (!(suffix == 'xls' || suffix == 'xlsx')) {
                    this.$message({
                        type: 'error',
                        message: '只能上传Excel文件'
                    });
                    return false
                }
            },
            successFileUpload(response) {
                const loading = this.$loading({
                    lock: true,
                    text: 'Loading',
                    spinner: 'el-icon-loading',
                    background: 'rgba(0, 0, 0, 0.7)'
                });

                setTimeout(() => {
                    loading.close();
                    if (response.code == '0200') {
                        this.$message({
                            type: 'success',
                            message: '操作成功'
                        });
                    } else {
                        this.$message({
                            type: 'error',
                            message: '操作失败,ERROR:' + response.msg
                        });
                    }
                }, 1000);

            }

        }
    };
</script>
<style lang="less" scoped>
    @import '../../style/mixin';

    .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        height: 50px;
        width: 50%;
    }

    .table_container {
        padding: 20px;
    }
</style>




