<script src="../../api/getData.js"></script>
<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-form :inline="true" :model="formInline" class="demo-form-inline">
                <el-form-item label="奖项类别">
                    <el-select v-model="category" filterable placeholder="奖项类别">
                        <el-option key="全部" value="全部"></el-option>
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="内容">
                    <el-input v-model="formInline.rank" placeholder="内容"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onQuery">查询</el-button>
                </el-form-item>
            </el-form>
            <el-table
                :data="tableData"
                border
                style="width: 100%">
                <el-table-column
                    prop="id"
                    label="ID"
                    min-width="120">
                </el-table-column>
                <el-table-column
                    prop="category"
                    label="类别"
                    min-width="200">
                </el-table-column>
                <el-table-column
                    prop="rank"
                    label="内容"
                    min-width="200">
                </el-table-column>
                <el-table-column
                    prop="score"
                    label="学分"
                    min-width="130">
                </el-table-column>
                <el-table-column
                    prop="createName"
                    label="创建人"
                    min-width="150">
                </el-table-column>
                <el-table-column
                    prop="createTime"
                    label="创建时间"
                    min-width="220">
                </el-table-column>
                <el-table-column
                    prop="updateTime"
                    label="更新时间"
                    min-width="220">
                </el-table-column>
                <el-table-column
                    prop="operation"
                    label="操作"
                    min-width="104">
                    <template slot-scope="scope">
                        <el-button @click="handleClick(scope.row)" type="text" size="small">授权</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                @current-change="handleCurrentChange"
                :current-page.sync="currentPage1"
                :page-size="10"
                layout="total, prev, pager, next"
                :total="total">
            </el-pagination>
        </div>
        <el-dialog title="审核授权" :visible.sync="dialogFormVisible" :modal-append-to-body="false">
            <el-form :model="form">
                <el-form-item label="ID" :label-width="formLabelWidth">
                    <el-input v-model="form.id" auto-complete="off" disabled></el-input>
                </el-form-item>
                <el-form-item label="类别" :label-width="formLabelWidth">
                    <el-input v-model="form.category" auto-complete="off" disabled></el-input>
                </el-form-item>
                <el-form-item label="内容" :label-width="formLabelWidth">
                    <el-input v-model="form.rank" auto-complete="off" disabled></el-input>
                </el-form-item>
                <el-form-item label="用户" :label-width="formLabelWidth">
                    <el-input v-model="form.user" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="onAuth">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import {rewardList, getOptions, getCategory, addAuthRecord} from '@/api/getData'

    export default {
        data() {
            return {
                tableData: [],
                total: 0,
                currentPage1: 1,
                formInline: {
                    rank: ''
                },
                form: {

                    id: '',
                    category: '',
                    rank: '',
                    user: '',


                },
                options: [],
                category: '',
                dialogFormVisible: false,
                formLabelWidth: '50px'
            }
        },
        beforeCreate: async function () {

            const res = await rewardList(1);
            if (res.code == '0200') {


                this.tableData = res.data;
                this.total = res.data[0].rows;

            } else {
                this.$message({
                    type: 'error',
                    message: res.msg
                });
            }
            const res1 = await getCategory();
            var data = [];
            for (var i = 0; i < res1.data.length; i++) {
                data[i] = {'label': res1.data[i], 'value': res1.data[i]}
            }
            this.options = data;
        },
        methods: {

            async onQuery() {

                if (this.category == '全部' || this.category == '') {
                    this.currentPage1 = 1;
                    await this.getRewardList(this.currentPage1)
                } else {
                    this.currentPage1 = 1;
                    await this.getData(this.currentPage1);
                }
            }, async handleCurrentChange() {
                await this.getData(this.currentPage1);
            }, async getData(page) {

                if (this.category == '全部' || this.category == '') {

                    const user = await this.getRewardList(page)
                    if (user.code == '0200') {

                        this.tableData = user.data;
                        this.total = user.data[0].rows;
                    } else {
                        this.$message({
                            type: 'error',
                            message: user.msg
                        });
                    }
                } else {
                    const user = await getOptions(this.category, this.formInline.rank, page)
                    if (user.code == '0200') {

                        this.tableData = user.data;
                        this.total = user.data[0].rows;
                    } else {
                        this.$message({
                            type: 'error',
                            message: user.msg
                        });
                    }

                }

            },
            async handleCurrentChange() {
                await this.getData(this.currentPage1);
            },
            async getRewardList(page) {
                const res = await rewardList(page);
                if (res.code == '0200') {


                    this.tableData = res.data;
                    this.total = res.data[0].rows;

                } else {
                    this.$message({
                        type: 'error',
                        message: res.msg
                    });
                }

            }, handleClick(row) {

                this.dialogFormVisible = true;
                this.form.id = row.id;
                this.form.category = row.category;
                this.form.rank = row.rank;
                this.form.score = row.score;

            },async onAuth() {

                const data = {
                    creditId: this.form.id,
                    account: this.form.user
                }
                const res = await addAuthRecord(data);
                if (res.code == '0200') {
                    this.$message({
                        type: 'success',
                        message: '授权成功'
                    });
                } else {
                    this.$message({
                        type: 'error',
                        message: res.msg
                    });

                }
                this.dialogFormVisible = false;
            }


        }
    };
</script>
<style lang="less" scoped>
    @import '../../style/mixin';

    /* .el-form-item {
         margin-right: 30px;
         margin-bottom: 0;
         height: 50px;
         width: 49%;
     }*/

    .query {
        position: absolute;
        right: 300px;
    }

    .table_container {
        padding: 20px;
    }
</style>




