<script src="../../api/getData.js"></script>
<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-form :inline="true" :model="formInline" class="demo-form-inline">
                <el-form-item label="班级">
                    <el-select v-model="classes" filterable placeholder="班级">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onQuery">查询</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="success" @click="getExcel">导出</el-button>
                </el-form-item>
            </el-form>
            <el-table
                :data="tableData"
                border
                style="width: 100%">
                <el-table-column
                    prop="id"
                    label="ID"
                    min-width="140">
                </el-table-column>
                <el-table-column
                    prop="account"
                    label="学号"
                    min-width="180">
                </el-table-column>
                <el-table-column
                    prop="name"
                    label="姓名"
                    min-width="180">
                </el-table-column>
                <el-table-column
                    prop="college"
                    label="学院"
                    min-width="200">
                </el-table-column>
                <el-table-column
                    prop="profession"
                    label="专业"
                    min-width="200">
                </el-table-column>
                <el-table-column
                    prop="classroom"
                    label="班级"
                    min-width="200">
                </el-table-column>
                <el-table-column
                    prop="score"
                    label="学分"
                    min-width="140">
                </el-table-column>
            </el-table>
            <div class="Pagination">
                <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage1"
                    :page-size="10"
                    layout="total, prev, pager, next"
                    :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    import {SergetExcel, getClasses, getUserByCondition} from '@/api/getData'
    import {serverUrl} from '@/config/env'

    export default {
        data() {
            return {

                formInline: {
                    rank: ''
                },
                tableData: [],
                options: [],
                classes: '',
                total: 0,
                currentPage1: 1,
            }
        },
        beforeCreate: async function () {
            const res = await getClasses();
            var data = [];
            for (var i = 0; i < res.data.length; i++) {
                data[i] = {
                    'label': '' + res.data[i].profession + res.data[i].classroom,
                    'value': JSON.stringify({"profession": res.data[i].profession, "classroom": res.data[i].classroom})
                }
                this.options = data;
            }
        }
        ,
        methods: {
            async onQuery() {
                await this.getData(1);
            }
            ,
            async handleCurrentChange() {
                await this.getData(this.currentPage1);
            }
            ,
            async getData(page) {

                let data = JSON.parse(this.classes);
                let college = JSON.parse(sessionStorage.getItem("data")).college;
                let profession = data.profession;
                let classroom = data.classroom;
                const user = await getUserByCondition(college, profession, classroom, page)
                if (user.code == '0200') {

                    this.tableData = user.data;
                    this.total = user.data[0].sum;
                } else {
                    this.$message({
                        type: 'error',
                        message: user.msg
                    });
                }
            }
            ,
            async getExcel() {

                if (this.classes==''){
                    this.$message({
                        type: 'error',
                        message: '条件不能为空'
                    });
                    return
                }
                const loading = this.$loading({
                    lock: true,
                    text: 'Loading',
                    spinner: 'el-icon-loading',
                    background: 'rgba(0, 0, 0, 0.7)'
                });

                let data = JSON.parse(this.classes);
                let college = JSON.parse(sessionStorage.getItem("data")).college;
                let profession = data.profession;
                let classroom = data.classroom;
                var url = serverUrl + "/user/getExcel?college=" + college + "&profession=" + profession + "&classroom=" + classroom;

                var xhr = new XMLHttpRequest();
                xhr.open('GET', url, true);
                xhr.responseType = 'blob';
                xhr.setRequestHeader('token', sessionStorage.getItem('token'))
                xhr.onload = function (e) {
                    setTimeout(() => {
                        loading.close();

                        if (this.status == 200) {
                            var blob = new Blob([this.response], {type: 'application/vnd.ms-excel'});
                            var downloadUrl = URL.createObjectURL(blob);
                            var a = document.createElement("a");
                            a.href = downloadUrl;
                            var _date = new Date();
                            a.download = _date.getUTCFullYear() + "-" + (_date.getUTCMonth() + 1) + "-" + _date.getUTCDate() +" "+ profession+"-"+classroom+"-学分信息排序表.xlsx";
                            document.body.appendChild(a);
                            a.click();
                        } else {
                            this.$message({
                                type: 'error',
                                message: '导出失败'
                            });
                        }
                    }, 1000);
                };
                xhr.send();

            }
        }
    }
    ;
</script>
<style lang="less" scoped>
    @import '../../style/mixin';

    /*.el-form-item {
        margin-right: 30px;
        margin-bottom: 0;
        height: 50px;
        width: 49%;
    }*/

    .query {
        position: absolute;
        right: 300px;
    }

    .table_container {
        padding: 20px;
    }

    .Pagination {
        display: flex;
        justify-content: flex-start;
        margin-top: 8px;
    }
</style>




