<script src="../../api/getData.js"></script>
<template>
    <div class="fillcontain">
        <div class="table_container">
            <el-row :gutter="20">
                <el-col :span="6">
                    <div class="grid-content bg-purple bg-f">
                        <div class="content-font">
                            <div class="nums">{{pending}}</div>
                            <div class="desc">待处理订单</div>
                        </div>
                        <div class="moreData">
                            <div><a href="javascript:void(0)" @click="toPending">查看更多</a></div>
                        </div>
                    </div>
                </el-col>
                <el-col :span="6">
                    <div class="grid-content bg-purple bg-s">
                        <div class="content-font">
                            <div class="nums">{{handled}}</div>
                            <div class="desc">已处理订单数</div>
                        </div>
                        <!--<div class="moreData">
                            <div>查看更多</div>
                        </div>-->
                    </div>
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>
    import {getOrderData} from '@/api/getData'

    export default {
        data() {
            return {
                pending: '',
                handled: ''
            };
        },
        beforeCreate: async function(){

            const  res = await getOrderData();
            if (res.code == '0200'){

                this.pending = res.data.pending;
                this.handled = res.data.handled;
            } else {
                this.$message({

                    type: 'error',
                    message: res.msg
                })
            }

        },
        methods: {
            toPending(){
                sessionStorage.setItem("router[0]", "工单管理");
                sessionStorage.setItem("router[1]", "待审核");
                this.$data.breadcrumbLevelArr = this.$store.state.viewState.breadcrumbLevel;
                this.$data.breadcrumbLevelArr[0].levelName = sessionStorage.getItem("router[0]");
                this.$data.breadcrumbLevelArr[1].levelName = sessionStorage.getItem("router[1]");
                this.$router.push({path: 'Pending'})
            }
        }
    };
</script>
<style lang="scss">
    .el-row {
        margin-bottom: 20px;
        &:last-child {
            margin-bottom: 0;
        }
    }

    .el-col {
        border-radius: 4px;
    }

    .bg-purple-dark {
        background: #99a9bf;
    }

    .bg-purple {
        background: #d3dce6;
    }

    .bg-purple-light {
        background: #e5e9f2;
    }

    .grid-content {
        border-radius: 4px;
        min-height: 136px;
        position: relative;
    }

    .row-bg {
        padding: 10px 0;
        background-color: #f9fafc;
    }

    .bg-f {
        background-color: #58B7FF;
    }

    .bg-s {
        background-color: #13CE66;
    }

    .bg-t {
        background-color: #F7BA2A;
    }

    .bg-h {
        background-color: #FF4949;
    }

    .content-font {
        padding-top: 15px;
        margin-left: 15px;
        .nums {
            font: {
                size: 40px;
            }
            color: #fff;
        }
        .desc {
            color: #fff;
            margin: {
                top: 10px;
            }
            font: {
                size: 15px;
                weight: 300;
            }
        }
    }

    .moreData {
        position: absolute;
        bottom: 1px;
        width: 100%;
        height: 25px;
        font: {
            size: 10px;
            weight: 300;
        }
        background: rgba(0, 0, 0, .1);
        text-align: center;
        color: #fff;
        div {
            line-height: 25px;
        }
    }
</style>



